import React, {useEffect, useRef} from 'react';
import Nav from "../../components/Nav/Nav";
import {LocomotiveScrollProvider} from "react-locomotive-scroll";
import Logo from "../../svg/Logo";
import {Link} from "react-router-dom";
import Arrow from "../../svg/arrow/Arrow";
import NewsOneSlider from "../../components/NewsOneSlider/NewsOneSlider";
import Contact from "../../components/Contact/Contact";

const NewsSecondPage = (props) => {
    const {slider} = props

    const containerRef = useRef(null)
    useEffect(() => {
        setTimeout(() => {

        }, 500)
    }, [])
    return (
        <>

            <LocomotiveScrollProvider options={
                {
                    direction: 'vertical',
                    smooth: true,
                    multiplier: 0.5,
                    reloadOnContextChange: false,
                }
            } containerRef={containerRef} watch={[]}>
                <Nav cssClass={"navigation navigation-black"}/>
                <section className={"news-one-page"} data-scroll-container ref={containerRef}>
                    <div className="news-one-page-logo">
                        <Logo color={'#373737'} cssClass={'news-one-page-logo__svg'}/>
                    </div>
                    <div className="news-one-page-title">
                        <Link to={'/news'} className="title-news-one-page">
                    <span className={"news-one-page__decoration"}>
                        <Arrow cssClass={'news-one-page__svg'} color={'#373737'}/>
                    </span>
                            <span className={"title news-one-page__text"}>всi Новини</span>
                        </Link>
                    </div>
                    <div className="news-one-page-wrapper-custom">
                        <h2>
                            Інтеграція стандарту кібер-безпеки 802.1x в IP-протокол CIAS
                        </h2>
                        <b>
                            Mar 24, 2020
                        </b>
                        <p>
                            Імплементація стандарту 802.1х в мережу підприємства дозволяє захистити дані та обладнання від кібер-атак. Вся критична інфраструктура потребує посилених механізмів аутентифікації всіх пристроїв в своїй мережі. Можливість того, що хтось здійснить кібер-атаку зсередини об’єкту є абсолютно реальною і тут у гру вступає протокол 802.1х:
                        </p>
                        <ul className={'news-one-page-container__lists'}>
                            <li><p>
                                802.1х використовується для безпечної мережевої аутентифікації. Якщо організація має справу з цінною та чутливою інформацією, їй необхідний безпечний метод передачі даних.
                            </p></li>
                            <li><p>
                                802.1х використовується для безпечної комунікації пристроїв з точками доступу (корпоративними маршрутизаторами). Історично такий підхід використовувався тільки у великих структурах як-то: корпорації, університети, військові об’єкти і клінічні установи; але через зростання кібер-загроз він все більше адаптується і в менш масштабних проектах
                            </p></li>
                            <li><p>
                                <strong>Оптимізація управління PIN-кодами</strong>
                                Нова опція Шифрування користувацьких PIN-кодів дозволяє додатково захищати коди
                                користувачів на постійній основі. При її активації оператор більше не зможе переглядати
                                задані користувачами коди чи вводити їх вручну. Коди, згенеровані в довільному порядку,
                                миттєво перестають діяти, а система потребує введення нового PIN користувача кожен раз,
                                коли він вперше авторизується на клавіатурі.
                            </p></li>
                            <li><p>
                                802.1х захищає мережу від несанкціонованого підключення пристроїв та завантаження вірусів, шкідливого ПЗ чи атак посередників (MITM).
                            </p></li>
                        </ul>
                        <p>Компанія CIAS завжди була в авангарді індустрії охорони периметрів і продовжує проактивно розробляти рішення для використання сенсорів на безпечних LAN-мережах з повним спектром власних IP-інтерфейсів.</p>
                        <p>Стандарт 802.1Х-2001 забезпечує механізм аутентифікації пристроїв, які хочуть отримати доступ до заданої LAN чи WLAN, встановлюючи лінк між клієнтом (наприклад, ноутбук) та мережею (світч або бездротова точка доступу), та може дозволяти або блокувати трафік між ними.</p>
                        <p>Клієнтський пристрій не має доступу через аутентифікатор до захищеного сегменту мережі, поки його ідентичність не була валідована та авторизована за допомогою даних, які можуть включати ім’я користувача/пароль або цифровий сертифікат.</p>
                        <p>Інтерфейс IP-DOORWAY, що сумісний з функціоналом аутентифікації згідно 802.1х, дозволяє підключати до мережі, що вимагає цього стандарту, будь-які цифрові продукти CIAS (СВЧ-бар’єри чи однопозиційні радари), а вібраційна сенсорна система SIOUX MEMS PRO2 (мережева із заводу) вже має його вбудованим в контролер.</p>
                        <p>Периметрові рішення CIAS використовують на найбільш чутливих об’єктах по всьому світу протягом майже 50 років завдяки своїй надійності, простоті в експлуатації та відповідності найжорсткішим вимогам.</p>
                        <section>
                            <img src={'/img/news_bit.jpg'} alt=""/>
                        </section>

                        <div className="news-one-page-container-slider">
                            <h6 className="title title-news-one-page-container-slider">
                                НОВИНИ
                            </h6>
                            <NewsOneSlider slider={slider}/>
                        </div>
                    </div>
                    <Contact cssClass={'news-one-page-container__news-one'}/>
                </section>
            </LocomotiveScrollProvider>
        </>
    )
}
export default NewsSecondPage