import React, {useEffect, useRef} from 'react';
import Logo from "../../svg/Logo";
import EducationItem from "../../components/EducationItem/EducationItem";
import EducationListUl from "../../components/EducationItem/EducationListUl";
import Contact from "../../components/Contact/Contact";
import Nav from "../../components/Nav/Nav";
import './educationPage.scss'
import Direction from "../../components/Direction/Direction";
import {LocomotiveScrollProvider, useLocomotiveScroll, Scroll,} from "react-locomotive-scroll";


const EducationPage = () => {
        const containerRef = useRef(null)

        const scrollingEducation = () => {
        }
        useEffect(() => {
            scrollingEducation()
        }, [])
        return (
            <>

                <LocomotiveScrollProvider
                    options={
                        {
                            smooth: true,
                            multiplier: 0.5,
                            direction: "vertical",
                            reloadOnContextChange: false,
                            resetNativeScroll: false,
                        }
                    }
                    watch={
                        []
                    }
                    containerRef={containerRef}
                >
                    <Nav cssClass={"navigation"}/>
                    <section className="education-page" ref={containerRef} data-scroll-container>
                        <div className={'education-hero'}>
                            <div className="education-logo">
                                <Logo color={'#ffffff'} cssClass={'education-logo__svg'}/>
                            </div>
                            <div className="educationPage__content">
                                <div className="wrapper">
                                    <h3 className="education__content__title">
                                        Educational activities
                                    </h3>
                                    <p className="education__content__sub-title">
                                        Avigilon - американська дочірня компанія Motorola Solutions.
                                    </p>
                                </div>
                            </div>
                        </div>
                        <div className="wrapper">
                            <article className="education__container">
                                <h6 className="title title-education__container"><span>Основи проектування та дизайну систем відеоспостереження та відеоаналітики</span><span>на базі рішень Avigilon</span>
                                </h6>
                                <p className="education__container__description">
                                    Відеоспостереження – одна з найпоширеніших та найбільш знайомих широкому загалу галузей
                                    індустрії безпеки, що часто хибно сприймається як дуже проста й зрозуміла. Однак,
                                    підходи до створення подібних систем, що застосовуються в приватних помешканнях,
                                    невеликих офісах та кафе з одного боку, та об’єктах критичної інфраструктури з іншого –
                                    суттєво різняться. Даний курс покликаний надати слухачам вичерпну інформацію щодо
                                    сучасних методів дизайну систем IP-відеоспостереження з урахуванням особливостей другої
                                    категорії об’єктів.
                                </p>
                            </article>
                            <ul className="education-program">
                                <EducationItem title={"Цільова аудиторія"}
                                               description={'проектанти та архітектори систем безпеки, presale-інженери, менеджери проектів, спеціалісти впровадження та експлуатації'}
                                />
                                <EducationItem title={"Тривалість курсу"}
                                               description={'15 академічних годин в залежності від попередньої підготовки слухачів'}
                                />
                                <EducationItem title={"Вартість"}
                                               description={'Безкоштовно для партнерів та клієнтів ТОВ «Фортісек»'}
                                />

                                <li className="education-program__list">
                                    <div className="education-program__item">
                                        <p className="education-program__item__title education-program__item__title--program">01.
                                            ВСТУпна частина</p>
                                        <ul className={'education-program__list--small'}>
                                            <li className="education__list-small">
                                                <p className="education_list-small__text">Попереднє дослідження об’єкту,
                                                    постановка правильних питань замовнику</p>
                                            </li>
                                            <li className="education__list-small">
                                                <p className="education_list-small__text">Формування концепції захисту
                                                    об’єкту засобами відеоспостереження</p>
                                            </li>
                                            <li className="education__list-small">
                                                <p className="education_list-small__text">Опрацювання схем інтеграції
                                                    системи відеоспостереження та інших систем безпеки на об’єкті</p>
                                            </li>
                                            <li className="education__list-small">
                                                <p className="education_list-small__text">Опрацювання алгоритмів взаємодії
                                                    системи відеоспостереження та особового складу охорони і операторів</p>
                                            </li>
                                            <li className="education__list-small education__list-small--no-decoration">
                                                <img className="education-program__item__list__img"
                                                     src={'./img/contact.jpg'} alt={''}/>
                                            </li>
                                        </ul>
                                    </div>
                                </li>
                                <EducationListUl title={"02.  ВСТУпна частина"}
                                                 itemDesc={['Модельний ряд відеокамер Avigilon', 'Рішення для обробки та запису відео', 'Системи репрезентації сигналів та взаємодії з оператором']}
                                />
                                <EducationListUl title={"03. Програмне забезпечення та відеоаналітика"}
                                                 itemDesc={['Основи нейронних мереж як бази аналітичних модулей', 'Використання відеоаналітики для рішення реальних задач на об’єкті: побудова нульового рубежу периметру, пошук за подобою, розпізнавання обличь та номерних знаків транспорту', 'Система ліцензування та розрахунок серверних потужностей для задіяння відеоаналітики']}
                                />
                                <EducationListUl title={"04.   Спеціальні рішення"}
                                                 itemDesc={['Системи відеоспостереження надвисокої чіткості (8-61Мп) та системи керування потоками даних', 'Інсталяції у вибухонебезпечних та інших промислових зонах з підвищеними вимогами до безпеки', 'Основи теплобачення та аналітики на термальному зображенні', 'Мобільні камери патрульних']}
                                />
                                <EducationListUl
                                    title={"05.  Система відеоспостереження як частина загальної концепції охорони"}
                                    itemDesc={['Інтеграція відеоспостереження із системами СКУД, внутрішньої та периметрової сигналізації, поняття відеопідтвердження за подією', 'Інтеграція з системами радіозв’язку Motorolla', 'Інші інтеграції (POS, Active Directory та ін.)']}
                                />
                                <EducationListUl
                                    title={"06.  Використання практичних інструментів Avigilon під час проектування "}
                                    itemDesc={['Пошук правильного рішення безпеки для ваших потреб - важливе рішення. Avigilon надає індивідуальні конфігурації безпеки - незалежно від того, в якій галузі ви перебуваєте.']}
                                />
                            </ul>
                        </div>
                        <div className="other-ways-education">
                            <h6 className="title title--other-ways-education">інші напрямки навчання</h6>
                            <div className="other-ways-education--container">
                                <Direction/>
                            </div>
                        </div>
                        <Contact cssClass={'other-ways-education--contact'}/>
                    </section>
                </LocomotiveScrollProvider>
            </>
        );
    }
;

export default EducationPage;