import React from 'react';
import Arrow from "../../svg/arrow/Arrow";

const ListDirection = (props) => {
    const {text, href, cssClass,cssSpan,cssSvg, cssColor, id} = props
    return (
        <li className={cssClass} id ={id}>
            <a href={href} className="competence__list__text"><span className="decoration-text">{text}</span><span className={cssSpan}>
               <Arrow cssClass={cssSvg} color={cssColor}/>
            </span>
            </a>
        </li>
    );
};

export default ListDirection;