import React, {useEffect, useState} from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import './newsSlider.scss'
import '../../pages/News/news.scss'
import SwiperCore, {Navigation, A11y, Autoplay, Controller} from 'swiper/core';
import SlideItem from "../SlideItem";
import {Link} from "react-router-dom";


SwiperCore.use([Navigation, A11y, Autoplay, Controller]);

const NewsSlider = () => {
    const [controlledSwiper, setControlledSwiper] = useState(null);

    return (

        <div className={'control-news-slider--container'}>
            <div className="control-news-slider">
                <Swiper
                    controller={{ control: controlledSwiper }}
                    navigation
                    speed={600}
                >
                    <SwiperSlide></SwiperSlide>
                    <SwiperSlide></SwiperSlide>
                    <SwiperSlide></SwiperSlide>
                    <SwiperSlide></SwiperSlide>
                </Swiper>
            </div>

            <Swiper
                onSwiper={setControlledSwiper}
                spaceBetween={50}
                slidesPerView={3}
                breakpoints={{
                    1680:{
                        spaceBetween:60,
                        slidesPerView:3,
                    },
                    1440:{
                        spaceBetween:60,
                        slidesPerView:3,
                    },
                    1053:{
                        slidesPerView:3,
                        spaceBetween:60,
                    },
                    769:{
                        slidesPerView:2,
                        spaceBetween:85,
                    },
                    768:{
                        slidesPerView:1.2,
                        spaceBetween:85,
                    },
                    320:{
                        slidesPerView:1,
                        spaceBetween:40,
                    }

                }}
            >
                <SwiperSlide>
                    <SlideItem text={{
                        imgHref: "./img/news_first.jpg",
                        date: "Mar 24, 2020",
                        description1: 'НОВА ВЕРСІЯ ПРОГРАМНОЇ ',
                        description2: 'ПЛАТФОРМИ ІКБ PROTÉGÉ GX',
                        descriptionCss: 'hero__desc__text news__desc__text',
                        color: "#373737",
                        articleCss: "hero-article-description",
                        link:"/news/description-full",
                    }}/>
                </SwiperSlide>
                <SwiperSlide>
                    <SlideItem text={{
                        imgHref: "./img/news_first.jpg",
                        date: "Mar 24, 2020",
                        description1: 'ІНТЕГРАЦІЯ СТАНДАРТУ БЕЗПЕКИ ',
                        description2: '802.1X В IP-ПРОТОКОЛ CIAS',
                        descriptionCss: 'hero__desc__text news__desc__text',
                        color: "#373737",
                        articleCss: "hero-article-description",
                        link:"/news/news2/description-full",
                    }}/>
                </SwiperSlide>
                <SwiperSlide>
                    <SlideItem text={{
                        imgHref: "./img/news_first.jpg",
                        date: "Mar 24, 2020",
                        description1: 'НОВА СЕРІЯ ВІДЕОКАМЕР',
                        description2: 'AVIGILON H5A DUAL HEAD',
                        descriptionCss: 'hero__desc__text news__desc__text',
                        color: "#373737",
                        articleCss: "hero-article-description",
                        link:"/news/news3/description-full",
                    }}/>
                </SwiperSlide>
                <SwiperSlide>
                    <div className="news__content news__content--last">
                        <Link to="/news" className="news__content--last__href">ВСІ НОВИНИ</Link>
                    </div>
                </SwiperSlide>
            </Swiper>
        </div>
    );
};

export default NewsSlider;