import React from 'react';
import ListDirection from "../ListDirection/ListDirection";

const Direction = () => {
    return (
        <div className="wrapper">
            <h4 className="direction__title">Напрямки навчання</h4>
            <article className="direction__content">
                <ul className="direction__ways">
                    <ListDirection text={'Системи периметральної сигналізації'}
                                   href={'/education'}
                                   cssClass={'competence__list--alternative'}
                                   cssSpan={'competence__list__text__decoration'}
                                   cssSvg={'direction__list__svg'}
                                   cssColor={'#2B2B2B'}

                    />
                    <ListDirection text={'Системи відеонагляду'}
                                   cssClass={'competence__list--alternative'}
                                   cssSpan={'competence__list__text__decoration'}
                                   cssSvg={'direction__list__svg'}
                                   cssColor={'#2B2B2B'}
                                   href={'/education'}
                    />
                    <ListDirection text={'Системи охоронного патрулювання'}
                                   cssClass={'competence__list--alternative'}
                                   cssSpan={'competence__list__text__decoration'}
                                   cssSvg={'direction__list__svg'}
                                   cssColor={'#2B2B2B'}
                                   href={'/education'}
                    />
                    <ListDirection text={'Системи контролю і управління доступом'}
                                   cssClass={'competence__list--alternative'}
                                   cssSpan={'competence__list__text__decoration'}
                                   cssSvg={'direction__list__svg'}
                                   cssColor={'#2B2B2B'}
                                   href={'/education'}
                    />
                    <ListDirection text={'Інтегровані комплекси безпеки'}
                                   cssClass={'competence__list--alternative'}
                                   cssSpan={'competence__list__text__decoration'}
                                   cssSvg={'direction__list__svg'}
                                   cssColor={'#2B2B2B'}
                                   href={'/education'}
                    />
                </ul>
            </article>
        </div>
    );
};

export default Direction;