import React, {useEffect, useRef} from 'react';
import Nav from "../../components/Nav/Nav";
import {LocomotiveScrollProvider} from "react-locomotive-scroll";
import Logo from "../../svg/Logo";
import EducationItem from "../../components/EducationItem/EducationItem";
import EducationListUl from "../../components/EducationItem/EducationListUl";
import Direction from "../../components/Direction/Direction";
import Contact from "../../components/Contact/Contact";

const EducationThirdPage = () => {
    const containerRef = useRef(null)

    const scrollingEducation = () => {
    }
    useEffect(() => {
        scrollingEducation()
    }, [])
    return (
        <>

            <LocomotiveScrollProvider
                options={
                    {
                        smooth: true,
                        multiplier: 0.5,
                        direction: "vertical",
                        reloadOnContextChange: false,
                        resetNativeScroll: false,
                    }
                }
                watch={
                    []
                }
                containerRef={containerRef}
            >
                <Nav cssClass={"navigation"}/>
                <section className="education-page" ref={containerRef} data-scroll-container>
                    <div className={'education-hero'}>
                        <div className="education-logo">
                            <Logo color={'#ffffff'} cssClass={'education-logo__svg'}/>
                        </div>
                        <div className="educationPage__content">
                            <div className="wrapper">
                                <h3 className="education__content__title">
                                    Educational activities
                                </h3>
                                <p className="education__content__sub-title">
                                    Avigilon - американська дочірня компанія Motorola Solutions.
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="wrapper">
                        <article className="education__container">
                            <h6 className="title title-education__container"><span>Функціональні можливості сучасних систем охоронного патрулювання на базі </span><span>обладнання Datix</span>
                            </h6>
                            <p className="education__container__description">
                                Дисциплінарний контроль особового складу є невід’ємною частиною процедур охорони на об’єктах будь-якого масштабу. Автоматизація такого контролю відбувається за допомогою широкого класу рішень та застосунків під загальною назвою «системи охоронного патрулювання». Курс покликаний інформувати слухачів щодо сучасного етапу розвитку систем, їх функціональних можливостей, різноманітних варіантах застосування.
                            </p>
                        </article>
                        <ul className="education-program">
                            <EducationItem title={"Цільова аудиторія"}
                                           description={'проектанти та архітектори систем безпеки, presale-інженери, менеджери проектів, спеціалісти впровадження та експлуатації, менеджери служб фізичної охорони, менеджери сервісних компаній'}
                            />
                            <EducationItem title={"Тривалість курсу"}
                                           description={'5 академічних годин в залежності від попередньої підготовки слухачів'}
                            />
                            <EducationItem title={"Вартість"}
                                           description={'Безкоштовно для партнерів та клієнтів ТОВ «Фортісек»'}
                            />

                            <li className="education-program__list">
                                <div className="education-program__item">
                                    <p className="education-program__item__title education-program__item__title--program">01.
                                        ВСТУпна частина</p>
                                    <ul className={'education-program__list--small'}>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Методи інтеграції: апаратна, протокольна, синхронізація баз даних</p>
                                        </li>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Ретроспективний огляд технологій охоронного патрулювання та обліку робочого часу: від механічних годинників охорони до додатків для смартфонів</p>
                                        </li>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Алгоритми правильного вибору технології патрулювання під задачі об’єкту</p>
                                        </li>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Питання безпеки, стійкості до зламу та клонування, підміни GPS-координат і т.д.</p>
                                        </li>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Варіанти застосування: від класичного контролю охорони до ОРВ сервісних компаній</p>
                                        </li>
                                        {/*<li className="education__list-small education__list-small--no-decoration">*/}
                                        {/*    <img className="education-program__item__list__img"*/}
                                        {/*         src={'/img/competence_dron.jpg'} alt={''}/>*/}
                                        {/*</li>*/}
                                    </ul>
                                </div>
                            </li>
                            <EducationListUl title={"02. Автономна RFID система патрулювання Datix Proxim"}
                                             itemDesc={["Принцип роботи і тактико-технічні показники системи",'Функціональні можливості звітнос']}
                            />
                            <EducationListUl title={"03. On-Line системи патрулювання Datix Wi-Trak Pro/Datix Nano "}
                                             itemDesc={["Принцип роботи і тактико-технічні показники Datix Wi-Trak Pro",'Принцип роботи і тактико-технічні показники Datix Nano','Комунікаційні можливості портативних зчитувачів','Аварійний функціонал Man Down']}
                            />
                            <EducationListUl title={"04. Віртуальна система патрулювання Datix2App"}
                                             itemDesc={['Сумісність програмного застосунку Datix2App','Основні переваги та можливості у порівнянні з традиційними апаратними рішеннями']}
                            />
                            <EducationListUl title={"05. Програмне забезпечення Datix Web Suite"}
                                             itemDesc={['Основні елементи системи: оператори, користувачі, тури, сервісні наряди, мітки, зчитувачі','Базові техніки і алгоритми програмування і налаштування системи','Робота з on-line зчитувачами через компонент RTS Monitor','Робота з GPS-координатами, функціонал геозонування','Способи інтеграції системи патрулювання у ПЗ верхнього рівня за допомогою XML','Хмарне vs локальне рішення: оптимізація витрат в залежності від господарської діяльності замовника']}
                            />
                        </ul>
                    </div>
                    <div className="other-ways-education">
                        <h6 className="title title--other-ways-education">інші напрямки навчання</h6>
                        <div className="other-ways-education--container">
                            <Direction/>
                        </div>
                    </div>
                    <Contact cssClass={'other-ways-education--contact'}/>
                </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default EducationThirdPage;