import React from 'react';

const EducationListUl = (props) => {
    const {title,itemDesc} = props
    console.log(itemDesc);
    return (
        <li className="education-program__list">
            <div className="education-program__item">
                <p className="education-program__item__title">{title}</p>
                <ul className={'education-program__list--small'}>
                {itemDesc.map((item, index)=>{
                    return <li key={index} className={'education__list-small'}><p className="education_list-small__text">{item}</p></li>
                })}
                </ul>
            </div>
        </li>
    );
};

export default EducationListUl;