import gsap from "gsap";
import {map, clamp, lerp} from "./utils";

gsap.registerPlugin();

export default class ScrollListScene {
    constructor(wrapper) {
        this.wrapper = wrapper;
        this.scroll = null;
        this.LIST_NODE_CLASS = ".c-list";
        this.ITEM_NODE_CLASS = ".c-list_item";
        this.HEAD_NODE_CLASS = ".c-list_head";
        this.IMAGE_NODE_CLASS = ".c-list_image";
        this.list = null;
        this.items = null;
        this.head = null;
        this.images = null;
        this.duration = window.innerWidth;
        this.boundings = {};
        this.animatebleProps = [];
        this.animatebleIndex = 0;

        this.delay = 0;

        this.raF = undefined;
        this.init_status = true;
    }

    init() {
        this.collectNodes();
        this.initWrapperNodeGeometry();
        this.calcBoundings();
        this.initItems();
        this.init_status = false;

        // this.items.forEach( (a, index) => {
        //     console.log(a,index, this.boundings.items[index]);
        // })
        window.addEventListener("resize", this.handleResize.bind(this));
    }

    render(scrollPos) {
        if (scrollPos) {
            this.update(scrollPos);
            // this.scroll = scrollPos;



            // }
            // this.animatebleIndex = i;
            // console.log(this.animatebleProps);
            this.animatebleProps.forEach((item, i) => {
                if(this.scroll.instance.direction === 'right') {
                    gsap.set(this.items[i], {
                        y: scrollPos > item.endAt ? item.to : item.prev,
                    });
                } else {
                    const win = scrollPos < item.endAt - window.innerWidth ? (scrollPos < item.endAt ? window.innerHeight - ((this.items.length * 60) - item.to) : item.prev) : item.prev

                    gsap.set(this.items[i], {
                        y: win,
                        // y: item.prev,
                    });
                    // this.animatebleProps[i].prev = win
                }

                gsap.set(this.images[i], {
                    opacity: item.imageProgress - .5,
                    scale: 2 - item.imageProgress
                });
            });
            this.renderLoop();
        }

    }

    update(scrollPos) {
        if (scrollPos) {
            this.animatebleProps.forEach((prop) => {
                const isCanMove = scrollPos > prop.startAt && scrollPos <= prop.endAt;

                prop.current = isCanMove
                    ? map(scrollPos, prop.startAt, prop.endAt, prop.from, prop.to)
                    : prop.current;
                    // : win ;
                prop.imageProgress = map(prop.current, prop.from, prop.to, 0, 1);
                prop.prev = isCanMove
                    ? lerp(prop.prev, prop.current, prop.amt)
                    : prop.current;
            });
        }

    }

    renderLoop() {
        if (!this.raF) {
            this.raF = requestAnimationFrame(() => this.render());
        }
    }

    stopRender() {
        if (this.raF) {
            window.cancelAnimationFrame(this.raF);
            this.raF = undefined;
        }
    }

    destroy() {
        this.stopRender();
        window.removeEventListener("resize", this.handleResize);
    }

    handleResize() {
        this.calcBoundings();

        // this.initItems()

        gsap.set(this.wrapper, {
            width: (this.items.length * this.duration) + this.duration
        });
    }

    collectNodes() {
        this.list = this.wrapper.querySelector(this.LIST_NODE_CLASS);
        this.items = this.wrapper.querySelectorAll(this.ITEM_NODE_CLASS);
        this.head = this.wrapper.querySelectorAll(this.HEAD_NODE_CLASS);
        this.images = this.wrapper.querySelectorAll(this.IMAGE_NODE_CLASS);


    }

    calcBoundings() {
        if (this.init_status === true) {
            this.boundings = {
                wrapper: this.wrapper.getBoundingClientRect(),
                list: this.list.getBoundingClientRect(),
                items: Array.from(this.items).map((item) => {
                    return item.getBoundingClientRect()
                }),
                head: Array.from(this.head).map((item) => {
                    return item.getBoundingClientRect()
                })
            };
            this.init_status = false;
            // console.log(this.boundings, this.init_status);
        }


    }

    initItems() {
        const listHeight = this.boundings.list.height;

            this.boundings.head.reduceRight((acc, curr, i) => {

                const allHedingsHeight = this.boundings.head.reduce((acc, curr) => {
                    return acc + curr.height;
                    // }, -(acc + listHeight) + (this.items[(this.items.length -1) - i].clientHeight - ((50 * (this.items.length - 1) + 10) -  acc)) );
                }, 0);
                gsap.set(this.items[i], {
                    y: listHeight - acc
                });

                this.animatebleProps.push({
                    current: listHeight - acc,
                    prev: listHeight - acc,
                    startAt: this.duration * i + this.boundings.wrapper.left,
                    endAt: this.duration * (i + 1) + this.boundings.wrapper.left,
                    to: allHedingsHeight - acc,
                    from: listHeight - acc,
                    amt: 1,
                    imageProgress: 0
                });

                return acc + curr.height;
            }, this.boundings.head[this.boundings.head.length - 1].height);

            this.animatebleProps.reverse();


    }

    initWrapperNodeGeometry() {
        gsap.set(this.wrapper, {
            width: ((this.items.length * this.duration) + this.duration)
        });
    }
}
