import React from 'react';
import './section-contact.scss'
import {Link} from "react-router-dom";

const Contact = (props) => {
    const {cssClass}= props
    return (
        <div className={cssClass }>
            <h6 className="title title--contact-section">Контакти</h6>
            <div className="contact-section-container">
                <Link to="/" className="category-link category-link--section"><div><span className="category-link__text">Office 304-306, Building 11, Kurortna</span><span className="category-link__text category-link__text--last"> str., Kyiv, 04075, Ukraine</span></div>
                </Link>
                <ul className={'contact-items-section'}>
                    <li className="contact__list contact__list--section"><a href="tel:+38(050)3530465" className="contact__list__href">Tel.: +38 (050) 353-04-65</a></li>
                    <li className="contact__list contact__list--section"><a href="mailto:info@fortisec.com.ua" className="contact__list__href">Mail to: info@fortisec.com.ua</a></li>
                </ul>
            </div>

        </div>
    );
};

export default Contact;