import React from 'react';

const EducationItem = (props) => {
    const {title, description} =props
    return (
        <li className="education-program__list education-program__list--alternative">
            <div className="education-program__item">
                <p className="education-program__item__title education-program__item__title--alternative">{title}</p>
                <p className="education-program__item__desc">{description}</p>
            </div>
        </li>
    );
};

export default EducationItem;