import React, {useEffect, useRef} from 'react';
import Hero from "../Hero/Hero";
import About from "../About/About";
import Competence from "../Competence/Competence";
import Education from "../Education/Education";
import Direction from "../Direction/Direction";
import News from "../News/News";
import Contact from "../Contact/Contact";
import {script} from './../utils/script'
import MapContact from "../MapContact/MapContact";
import {LocomotiveScrollProvider, useLocomotiveScroll,} from "react-locomotive-scroll";
import Nav from "../../components/Nav/Nav";
import BrandsNew from "../Brands/BrandsNew";
import Servive from "../Service/Service";
import EducationPageMobile from "../EducationPageMobile/EducationPageMobile";
import BrandMobile from "../BrandMobile/BrandMobile";
import ServicesMobile from "../SevicesMobile/ServicesMobile";

const MainPage = (props) => {
        const {scroll} = useLocomotiveScroll()
        const
            containerRef = useRef(null)

        const scrolling = () => {
            script()
        }
        useEffect(() => {
            scrolling()
        }, [])
        return (
            <LocomotiveScrollProvider

                options={
                    {

                        smooth: true,
                        multiplier: 0.89,
                        direction: "horizontal",
                        getDirection: true,
                        reloadOnContextChange: false,
                        tablet: {
                            breakpoint: 1024,
                            direction: "vertical",
                        },
                        smartphone: {
                            smooth: false
                        },
                    }
                }
                watch={
                    []
                }
                containerRef={containerRef}>
                <Nav cssClass={"navigation"}/>
                <main className={"main"} id={'main'} data-scroll-container data-scroll ref={containerRef}>
                    <Hero scroll={scroll}/>
                    <About/>
                    <BrandMobile/>
                    <BrandsNew/>
                    <Competence/>
                    <EducationPageMobile/>
                    <Education/>
                    <Direction/>
                    <ServicesMobile/>
                    <News/>
                    <Servive/>
                    <Contact/>
                    <MapContact/>
                </main>

            </LocomotiveScrollProvider>

        );
    }
;

export default MainPage;

