import React, {useEffect, useRef} from 'react';
import './NewsOnePage.scss'
import {Link} from "react-router-dom";
import Arrow from "../../svg/arrow/Arrow";
import Logo from "../../svg/Logo";
import Contact from "../../components/Contact/Contact";
import NewsOneSlider from "../../components/NewsOneSlider/NewsOneSlider";
import Nav from "../../components/Nav/Nav";
import {LocomotiveScrollProvider} from "react-locomotive-scroll";

const NewsOnePage = (props) => {
    const {slider} = props

    const containerRef = useRef(null)
    useEffect(() => {
        setTimeout(() => {

        }, 500)
    }, [])
    return (
        <>
            <LocomotiveScrollProvider options={
                {
                    direction: 'vertical',
                    smooth: true,
                    multiplier: 0.5,
                    reloadOnContextChange: false,
                }
            } containerRef={containerRef} watch={[]}>
                <Nav cssClass={"navigation navigation-black"}/>
                <section className={"news-one-page"} data-scroll-container ref={containerRef}>
                    <div className="news-one-page-logo">
                        <Logo color={'#373737'} cssClass={'news-one-page-logo__svg'}/>
                    </div>
                    <div className="news-one-page-title">
                        <Link to={'/news'} className="title-news-one-page">
                    <span className={"news-one-page__decoration"}>
                        <Arrow cssClass={'news-one-page__svg'} color={'#373737'}/>
                    </span>
                            <span className={"title news-one-page__text"}>всi Новини</span>
                        </Link>
                    </div>
                    <div className="news-one-page-wrapper-custom">
                        <h2>
                            Нова версія програмної платформи ІКБ Protégé GX
                        </h2>
                        <b>
                            Mar 24, 2020
                        </b>
                        <p>
                            Компанія Integrated Control Technology анонсувала вихід нової версії програмного комплексу Protégé GX 4.3.307. Дане оновлення включає наступний перелік нових функції та оптимізацій:
                        </p>
                        <ul className={'news-one-page-container__lists'}>
                            <li><p>
                                <strong>Сервіс завантаження по одному запису</strong>
                                Створено новий програмний сервіс завантаження контролерів, що є додатком до вже існуючого механізму завантаження даних Protégé GX та являє собою ідеальне рішення для об’єктів з 5-200 системними контролерами. Даний сервіс завантажуватиме зміни до таких елементів як користувачі, рівні доступу чи розклади за секунди, що раніше забирало до кількох хвилин.
                            </p></li>
                            <li><p>
                                <strong>Інтеграція з Assa Abloy DSR</strong>
                                Інтеграція програмних платформ Protégé GX та  Assa Abloy DSR (Door Service Router) з сумісними IP-замками дозволяє комбінувати надійні замкові системи з електронним контролем доступу, охоронною сигналізацією та автоматикою будівлі.
                            </p></li>
                            <li><p>
                                <strong>Оптимізація управління PIN-кодами</strong>
                                Нова опція Шифрування користувацьких PIN-кодів дозволяє додатково захищати коди користувачів на постійній основі. При її активації оператор більше не зможе переглядати задані користувачами коди чи вводити їх вручну. Коди, згенеровані в довільному порядку, миттєво перестають діяти, а система потребує введення нового PIN користувача кожен раз, коли він вперше авторизується на клавіатурі.
                            </p></li>
                            <li><p>
                                <strong>Оновлення процедур безпеки на контролерах</strong>
                                Після оновлення апаратної прошивки контролера тепер необхідно змінювати пароль, якщо використовується встановлений за замовчанням. Переключення системного контролеру в заводський режим поверне всі налаштування до початкових, включаючи IP адресу та дані логіну. Всі системні контролери в майбутньому поставлятимуться з активованим HTTPS, що обов’язково вимагатиме встановлення індивідуальних ім’я користувача та пароля.
                            </p></li>
                            <li><p>
                                <strong>Оновлені теми Web-клієнту</strong>
                                Використовуючи Web-клієнт оператор відтепер має можливість вибрати тему (світлу чи темну) та колір інтерфейсу під час авторизації. Вибір зберігається при наступних входах з того ж браузеру.
                            </p></li>
                            <li><p>
                                <strong>Підтримка OSDP V2</strong>
                                Зчитувачі сумісні з OSDP V2.1.5. можуть бути підключення до системних контролерів Protégé або відповідних розширювачів (після оновлення апаратної прошивки) з використанням існуючих портів RS-485.
                            </p></li>
                            <li><p>
                                <strong>Інтеграція з Genetec Security Center Omnicast</strong>
                                Нові можливості існуючого широкого інтеграційного портфоліо – Genetec Security Center Omnicast – IP-відеосистема, що зростає разом з потребами замовника та надає переваги використання сучасного апаратного забезпечення і аналітичних технологій.
                            </p></li>
                        </ul>
                        <section>
                            <img src={'/img/news_bit.jpg'} alt=""/>
                        </section>

                    <div className="news-one-page-container-slider">
                        <h6 className="title title-news-one-page-container-slider">
                            НОВИНИ
                        </h6>
                        <NewsOneSlider slider={slider}/>
                    </div>
                    </div>
                    <Contact cssClass={'news-one-page-container__news-one'}/>
                </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default NewsOnePage;