import React from 'react';
import {Link} from "react-router-dom";
import Arrow from "../../svg/arrow/Arrow";

const EducationList = (props) => {
    const {img, title, description, link, linkText, educationDate, linkCss} = props
    return (
        <div className={'education__list'}>
            <div className="education__list__img">
                <img src={img} alt=""/>
            </div>
            <div className="education__list__content">
                <div className="education__list__description-container">
                    <h5 className="education__list__title">
                        {title}
                    </h5>
                    <p className="education__list__description">{description}</p>
                </div>
                <div className="education__list__href">
                    <Link href={link} className={linkCss} to={link}>{linkText}</Link>
                    <p className="education__list-date">
                        {educationDate}
                    </p>
                </div>
            </div>
            <Link to={link} className="education__arrow">
                <Arrow color={"#373737"} cssClass ={"education__arrow__svg"}/>
            </Link>
        </div>

    );
};

export default EducationList;