import React, {useEffect, useRef} from 'react';
import './NewsPage.scss'
import Logo from "../../svg/Logo";
import EducationList from "../../components/EducationList/EducationList";
import Nav from "../../components/Nav/Nav";
import {script} from './../utils/script'
import {LocomotiveScrollProvider, useLocomotiveScroll} from "react-locomotive-scroll";
const NewsPage = () => {
    const containerRef = useRef(null)
    const { scroll } = useLocomotiveScroll()
    console.log(scroll);
    useEffect(()=>{
       setTimeout(()=>{},500)
       script()
   },[])
    return (
        <>
            <LocomotiveScrollProvider
                options={
                    {
                        smooth: true,
                        multiplier: 0.5,
                        direction: "vertical",
                        reloadOnContextChange: false,

                        tablet: {
                            breakpoint: 1024,
                            direction: "vertical",
                        },
                        smartphone: {
                            smooth: false
                        },
                        resetNativeScroll: false,
                    }
                }
                watch={
                    []
                }
                containerRef={containerRef}
            >
                <Nav cssClass={"navigation navigation-black"}/>
        <section className="educationPage" data-scroll-container  ref={containerRef}>

            <div className="educationPage__logo">
                <Logo color={"#373737"} cssClass ={"educationPage__logo__svg"}/>
                <h3 className={'title title--educationPage'} data-scroll-speed={5}>News</h3>
            </div>
            <ul className="education__content">
                <EducationList img={'./img/imgCity.jpg'} title={'НОВА ВЕРСІЯ ПРОГРАМНОЇ ПЛАТФОРМИ ІКБ PROTÉGÉ GX'}
                                description={'Компанія Integrated Control Technology анонсувала вихід нової версії програмного комплексу Protégé GX 4.3.307. Дане оновлення включає наступний перелік нових функції та оптимізацій.'}
                                link={'/news/description-full'} linkText={'ПОДРОБНЕЕ'} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
                <EducationList img={'./img/news_train.jpg'} title={'ІНТЕГРАЦІЯ СТАНДАРТУ КІБЕР-БЕЗПЕКИ 802.1X В IP-ПРОТОКОЛ CIAS'}
                               description={'Імплементація стандарту 802.1х в мережу підприємства дозволяє захистити дані та обладнання від кібер-атак. Вся критична інфраструктура потребує посилених механізмів аутентифікації всіх пристроїв в своїй мережі. Можливість того, що хтось здійснить кібер-атаку зсередини об’єкту є абсолютно реальною і тут у гру вступає протокол 802.1х:'}
                               link={'/news/news2/description-full'} linkText={'ПОДРОБНЕЕ'} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
                <EducationList img={'./img/imgCity.jpg'} title={'НОВА СЕРІЯ ВІДЕОКАМЕР AVIGILON H5A DUAL HEAD'}
                               description={'Avigilon H5A Dual Head це економічна двосенсорна відеокамера в компактному корпусі, що пропонує збільшену зону огляду та ІЧ-підсвітку і допомагає захистити сліпі зони в таких складних місцях як: коридори, сходові марші, перехрестя проходів, в’їзди та виїзди з паркінгу. Камера малого форм-фактору оснащена алгоритмами відеоаналітики наступного покоління для подальшого посилення ситуаційного контролю через можливість швидкого реагування на потенційно критичні події.'}
                               link={'/news/news3/description-full'} linkText={'ПОДРОБНЕЕ'} educationDate={'Mar 24, 2020'} linkCss ={'education__list__href__decoration'}
                />
            </ul>
            {/*<div className="educationPage__href" data-scroll-target={"nav"}>*/}
            {/*    <Link className = {'educationPage__href__decoration'} to={'/news/first-page'}>1</Link>*/}
            {/*    <Link className = {'educationPage__href__decoration educationPage__href__decoration--active'} to={'/news'}>2</Link>*/}
            {/*    <Link className = {'educationPage__href__decoration'} to={'/news/third-page'}>3</Link>*/}
            {/*</div>*/}
            {/*<Contact cssClass={'section-contact-wrapper'}/>*/}
        </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default NewsPage;