import React, {useEffect, useRef} from 'react';
import Logo from "../../svg/Logo";
import {Link} from "react-router-dom";
import Arrow from "../../svg/arrow/Arrow";
import NewsOneSlider from "../../components/NewsOneSlider/NewsOneSlider";
import Contact from "../../components/Contact/Contact";
import {LocomotiveScrollProvider} from "react-locomotive-scroll";
import Nav from "../../components/Nav/Nav";

const NewsThirdPage = (props) => {
    const {slider} = props

    const containerRef = useRef(null)
    useEffect(() => {
        setTimeout(() => {

        }, 500)
    }, [])
    return (
        <>

            <LocomotiveScrollProvider options={
                {
                    direction: 'vertical',
                    smooth: true,
                    multiplier: 0.5,
                    reloadOnContextChange: false,
                }
            } containerRef={containerRef} watch={[]}>
                <Nav cssClass={"navigation navigation-black"}/>
                <section className={"news-one-page"} data-scroll-container ref={containerRef}>
                    <div className="news-one-page-logo">
                        <Logo color={'#373737'} cssClass={'news-one-page-logo__svg'}/>
                    </div>
                    <div className="news-one-page-title">
                        <Link to={'/news'} className="title-news-one-page">
                    <span className={"news-one-page__decoration"}>
                        <Arrow cssClass={'news-one-page__svg'} color={'#373737'}/>
                    </span>
                            <span className={"title news-one-page__text"}>всi Новини</span>
                        </Link>
                    </div>
                    <div className="news-one-page-wrapper-custom">
                        <h2>
                            Нова серія відеокамер Avigilon H5A Dual Head
                        </h2>
                        <b>
                            Mar 24, 2020
                        </b>
                        <p>Avigilon H5A Dual Head це економічна двосенсорна відеокамера в компактному корпусі, що пропонує збільшену зону огляду та ІЧ-підсвітку і допомагає захистити сліпі зони в таких складних місцях як: коридори, сходові марші, перехрестя проходів, в’їзди та виїзди з паркінгу. Камера малого форм-фактору оснащена алгоритмами відеоаналітики наступного покоління для подальшого посилення ситуаційного контролю через можливість швидкого реагування на потенційно критичні події.</p>
                        <h3>Збільшений огляд, мінімальні сліпі зони </h3>
                        <p>Відеокамера забезпечує збільшений огляд, мінімізуючи сліпі зони для більшої безпеки у єдиній точці інсталяції всередині приміщень: довгі переходи, L-образні перехрестя, сходи з можливістю одночасного моніторингу підйому та спуску, та будь-які інші місця, що потребують двох камер з якісним зображенням, встановлених в безпосередній близькості одна від одної.</p>
                        <h3>Відеоаналітика наступного покоління</h3>
                        <p>Детекція більшої кількості об’єктів з посиленими можливостями класифікації та точності розпізнавання для швидшого реагування, навіть у сценах з натовпом. Аналітичні можливості камери H5A Dual Head включають виявлення та категорування об’єктів, підтримку технологій розпізнавання обличь та пошуку за подобою для прискорення відповіді оператора в режимі реального часу або під час розбору інцидентів, підрахунок людей та детекцію масок для запобігання поширення COVID-19.  </p>
                        <h3>Економічне рішення</h3>
                        <p>Використовуючи двосенсорні камери замовники мають можливість зекономити кошти за рахунок зменшення числа встановлених пристроїв, мережевої інфраструктури, живлення і кабельних ліній, трудових витрат та ліцензійних платежів.</p>
                        <section>
                            <img src={'/img/news_bit.jpg'} alt=""/>
                        </section>

                        <div className="news-one-page-container-slider">
                            <h6 className="title title-news-one-page-container-slider">
                                НОВИНИ
                            </h6>
                            <NewsOneSlider slider={slider}/>
                        </div>
                    </div>
                    <Contact cssClass={'news-one-page-container__news-one'}/>
                </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default NewsThirdPage;