import React, {useEffect} from 'react';
import Logo from "../../svg/Logo";
import Close_svg from "../../svg/Close_svg";
import Arrow from "../../svg/arrow/Arrow";
import './Nav.scss'
import {Link} from "react-router-dom";
import {useLocomotiveScroll} from "react-locomotive-scroll";

const Nav = (props) => {

    const {cssClass} = props

    const {scroll} = useLocomotiveScroll()

    const scrolling = (e, id) =>{
       e.preventDefault()
       scroll.scrollTo(id)
   }



    const navigationAction =()=>{
        const menuContainer = document.querySelector('.nav-small')
        const menuShow = document.querySelector('.navigation-container-wrapper')
        menuContainer.addEventListener('click', (e) => {
            if (e.currentTarget.classList.contains('nav-small') && !e.target.classList.contains('nav-small__lang')) {
                menuShow.classList.add('navigation-container-wrapper--active')
            }
        })
        const closeBtn = document.querySelector('.btn--close')
        menuShow.addEventListener('click', (e) => {
            const cursorTarget = e.target
            console.log(cursorTarget);
            if (cursorTarget.classList.contains('navigation-container-wrapper')) {
                menuShow.classList.remove('navigation-container-wrapper--active')
            }
        })
        closeBtn.addEventListener('click', e => {
            if (e.currentTarget === closeBtn) {
                menuShow.classList.remove('navigation-container-wrapper--active')
            }
        })
    }

    useEffect(()=>{
            navigationAction()
    },[])
    return (
        <nav className={cssClass}>
            <div className="nav-small">
                <div className="nav-small__content">
                    <div className="nav-small__content__decoration">
                        <div className="nav-small__content__decoration__line"></div>
                        <div className="nav-small__content__decoration__line"></div>
                    </div>
                    <p className="nav-small__text">МЕНЮ</p>
                </div>
                <a href="#" className="nav-small__lang">EN</a>
            </div>
            <div className="navigation-container-wrapper">
                <div className="navigation-container">
                    <div className="navigation-container__header">
                        <div className="logo-dark">
                            <Logo color="#373737" cssClass ={'logo-black'}/>
                        </div>
                        <div className="navigation-container__lang">
                            <a href={'#'} className="navigation-container__lang__text">UA</a><a href={'#'} className="navigation-container__lang__text navigation-container__lang__text--en">EN</a>
                            <button className="btn btn--close">
                                <Close_svg color="#373737"/>
                            </button>
                        </div>
                    </div>
                    <div className="navigation-container__content">
                        <ul className="navigation-list">
                            <li className="list-items list-items-main"><Link to="/" className="list-items__href"><span className="list-items__href__text">головна</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items list-items--about" onClick={(e) =>scrolling(e,'#about')}><Link to="/#about" className="list-items__href"><span className="list-items__href__text">про нас</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items" onClick={(e) =>{scrolling(e,'#brand')}}><Link to="/#brand" className="list-items__href"><span className="list-items__href__text">бренди</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items list-items-competence"onClick={(e) =>scrolling(e, '#competence')} ><Link to="/#competence" className="list-items__href"><span className="list-items__href__text">Компетенції</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items education-test" onClick={(e) =>scrolling(e, '#education')}><Link to="/#education" className="list-items__href"><span className="list-items__href__text">навчання</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items" onClick={(e)=>scrolling(e,'#services')}><Link to="/#servisec" className="list-items__href"><span className="list-items__href__text">послуги</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items"><Link to="/news" className="list-items__href"><span className="list-items__href__text">новини</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                            <li className="list-items list-items-contacts" onClick={(e)=>scrolling(e,'#contact')} ><Link to="/#contact" className="list-items__href"><span className="list-items__href__text">контакти</span></Link><span className="list-items__href__decoration"> <Arrow color="#373737" cssClass={'menu-arrow'}/></span></li>
                        </ul>
                    </div>
                    <div className="navigation-container__footer">
                        <a href="tel:+38(050)3530465" className="link-action">Tel.: +38 (050) 353-04-65</a>
                        <a href="mailto:info@fortisec.com.ua" className="link-action">Mail to: info@fortisec.com.ua</a>
                    </div>
                </div>
            </div>
        </nav>
    );
};

export default Nav;