import React, {useEffect} from 'react';
import './mapContact.scss'
import { Loader } from "@googlemaps/js-api-loader"
import { useLocomotiveScroll } from "react-locomotive-scroll"

const MapContact = () => {
    const mapRef = React.createRef();
    const { scroll } = useLocomotiveScroll()

    function handleMouseWheel(e) {
        if(e.ctrlKey === true) {
            e.preventDefault();
            scroll.stop()
        } else {
            scroll.start()
        }
    }

    useEffect(() => {
        const loader = new Loader({
            apiKey: "AIzaSyAcyLbeWDtTd0uDBIOnUrn33-L5Xqw8rsY",
            version: "weekly"
        });

        const mapObserver = new IntersectionObserver(entries => {
            entries.forEach((entry) => {
                if(entry.isIntersecting) {
                    loader.load().then(() => {
                        let map = new window.google.maps.Map(document.getElementById("map"), {
                            center: { lat: 50.5287559, lng: 30.3547129 },
                            zoom: 17,
                            disableDefaultUI: true,
                            styles: [
                                {
                                    "featureType": "all",
                                    "elementType": "labels.text.fill",
                                    "stylers": [
                                        {
                                            "saturation": 36
                                        },
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 40
                                        }
                                    ]
                                },
                                {
                                    "featureType": "all",
                                    "elementType": "labels.text.stroke",
                                    "stylers": [
                                        {
                                            "visibility": "on"
                                        },
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 14
                                        }
                                    ]
                                },
                                {
                                    "featureType": "all",
                                    "elementType": "labels.icon",
                                    "stylers": [
                                        {
                                            "visibility": "off"
                                        }
                                    ]
                                },
                                {
                                    "featureType": "administrative",
                                    "elementType": "geometry.fill",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 20
                                        }
                                    ]
                                },
                                {
                                    "featureType": "administrative",
                                    "elementType": "geometry.stroke",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 15
                                        },
                                        {
                                            "weight": 1.2
                                        }
                                    ]
                                },
                                {
                                    "featureType": "landscape",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 20
                                        }
                                    ]
                                },
                                {
                                    "featureType": "poi",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 21
                                        }
                                    ]
                                },
                                {
                                    "featureType": "road.highway",
                                    "elementType": "geometry.fill",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 15
                                        }
                                    ]
                                },
                                {
                                    "featureType": "road.highway",
                                    "elementType": "geometry.stroke",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 29
                                        },
                                        {
                                            "weight": 0.2
                                        }
                                    ]
                                },
                                {
                                    "featureType": "road.arterial",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 18
                                        }
                                    ]
                                },
                                {
                                    "featureType": "road.local",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 14
                                        }
                                    ]
                                },
                                {
                                    "featureType": "transit",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 19
                                        }
                                    ]
                                },
                                {
                                    "featureType": "water",
                                    "elementType": "geometry",
                                    "stylers": [
                                        {
                                            "color": "#000000"
                                        },
                                        {
                                            "lightness": 15
                                        }
                                    ]
                                }
                            ]
                        });

                        const marker = new window.google.maps.Marker({
                            position: { lat: 50.5287559, lng: 30.3547129 },
                            map: map,
                        });
                    });

                    mapObserver.unobserve(entry.target)
                }
            });

        });
        mapObserver.observe(mapRef.current)
    }, [mapRef])
    return (
        <section ref={mapRef} className={'map-container'} data-scroll-section>
            <div onWheel={handleMouseWheel} id={"map"} className={'map'}>

            </div>
        </section>
    );
};

export default MapContact;