import React, {useEffect, useState} from 'react';
import {Swiper, SwiperSlide} from 'swiper/react';
import '../../components/NewsSlider/newsSlider.scss'
import '../../pages/News/news.scss'
import SwiperCore, {Navigation, A11y, Autoplay, Controller} from 'swiper/core';
import Arrow from "../../svg/arrow/Arrow";


SwiperCore.use([Navigation, A11y, Autoplay, Controller]);

const NewsOneSlider = (props) => {
    const [controlledSwiper, setControlledSwiper] = useState(null);
    const [array, NewArray] = useState([])
    const {slider} = props
    const {slideNumber, slideContent} = slider
    console.log(slideContent);
    const slideArr = []

    const makeFirstSlide = () => {
        for (let i = 0; i < slideNumber; i++) {
            const tag = ""
            slideArr.push(tag)
        }
    }
    const makeArr = () => {
        NewArray(slideArr)
    }

    useEffect(() => {
        makeFirstSlide()
        makeArr()
    }, [])

    return (
        <>
            <div className="control-news-one-slider">
                <Swiper
                    controller={{control: controlledSwiper}}
                    navigation
                    speed={650}
                >
                    {array.map((item,i) => {
                        return <SwiperSlide key={i}>{item}</SwiperSlide>
                    })}
                </Swiper>
            </div>
                <div className="new-one-slider-container">

                <Swiper
                    onSwiper={setControlledSwiper}
                    spaceBetween={50}
                    slidesPerView={2.2}
                >
                    {slideContent.map((item, i)=>{
                        return  <SwiperSlide key ={i}>
                            <div className="news__content">
                                <div className="news__content__img">
                                    <img src={item.imgHref} alt="" className="slide__img"/>
                                </div>
                                <article className={item.articleCss}>
                                    <p className="hero-desc__date">{item.date}</p>
                                    <a href={"#"} className="news__desc__container">
                                        <div className={item.descriptionCss}>
                                            <p className={'news__desc__text-decoration'}><span className="news__desc__text-decoration__border">{item.description1}</span></p>
                                            <p className={'news__desc__text-decoration'}><span className="news__desc__text-decoration__border">{item.description2}</span></p>
                                        </div>
                                        <Arrow color={item.color} cssClass={"slide-hero-arrow"}/>
                                    </a>
                                </article>
                            </div>
                        </SwiperSlide>
                    })}
                </Swiper>
                </div>
        </>
    )
}




export default NewsOneSlider;

