import React from 'react';
import './EductionPageMobile.scss'
import ListDirection from "../../components/ListDirection/ListDirection";
import Arrow from "../../svg/arrow/Arrow";

const EducationPageMobile = () => {
    return (
        <>
            <section className={'education-mobile'} data-scroll-section>
                <h4 className="title title--education-mobile">Освітня діяльність</h4>
                <h6 className="direction__title direction__title-sub">Напрямки навчання</h6>
                <div className="wrapper wrapper--decoration">
                    <article className="direction__content">
                        <ul className="direction__ways">
                            <ListDirection text={'Системи периметральної сигналізації'}
                                           cssClass={'competence__list--alternative'}
                                           cssSpan={'competence__list__text__decoration'}
                                           cssSvg ={'direction__list__svg'}
                                           cssColor={'#2B2B2B'} href={'/education/first'}
                            />
                            <ListDirection text={'Системи відеонагляду'}
                                           cssClass={'competence__list--alternative'}
                                           cssSpan={'competence__list__text__decoration'}
                                           cssSvg ={'direction__list__svg'}
                                           cssColor={'#2B2B2B'} href={'/education/second'}
                            />
                            <ListDirection text={'Системи охоронного патрулювання'}
                                           cssClass={'competence__list--alternative'}
                                           cssSpan={'competence__list__text__decoration'}
                                           cssSvg ={'direction__list__svg'}
                                           cssColor={'#2B2B2B'} href={'/education/third'}
                            />
                            <ListDirection text={'Системи контролю і управління доступом'}
                                           cssClass={'competence__list--alternative'}
                                           cssSpan={'competence__list__text__decoration'}
                                           cssSvg ={'direction__list__svg'}
                                           cssColor={'#2B2B2B'} href={'/education'}
                            />
                            <ListDirection text={'Інтегровані комплекси безпеки'}
                                           cssClass={'competence__list--alternative'}
                                           cssSpan={'competence__list__text__decoration'}
                                           cssSvg ={'direction__list__svg'}
                                           cssColor={'#2B2B2B'} href={'/education'}
                            />
                        </ul>
                    </article>
                </div>
                <section className="standard-description standard-description-education" data-scroll-section>
                    <div className="wrapper">
                        <p className="category-sub-title">Завдання наших семінарів - систематизувати і доповнити ваш <span
                            className="category-sub-title__decoration">досвід роботи з системами безпеки!</span></p>
                        <article className="standard-description__container">
                            <p className="standard-description__container__text standard-description__container__text--education">Fortisec – це команда однодумців та професіоналів ринку охоронних
                                систем з
                                індивідуальною практикою не менше 20 років. Перш за все, це люди, що успішно
                                творюють та впроваджують одні з наймасштабніших та найбільш складних інтегрованих
                                комплексів безпеки в Україні. Це ідеї, знання та експертиза, що дозволяють убезпечувати
                                дійсно складні, критично важливі</p>
                            <p className="standard-description__container__text standard-description__container__text--education-last">Fortisec – це компанія-консультант та проектний дистриб’ютор, що
                                надає
                                кваліфіковані послуги з попереднього вивчення об’єктів, моделювання загроз,
                                створення концепцій охорони за допомогою найсучасніших апаратно-програмних
                                засобів, постачання відповідного обладнання, повного спектру комерційної та
                                технічної підтримки партнерів на всіх етапах життєвого циклу проектів.</p>
                        </article>
                        <div className="education-mobile-img">
                            <img src="./img/mobile_education_last_img.jpg" alt=""/>
                        </div>
                    </div>
                </section>
            </section>
        </>
    );
};

export default EducationPageMobile;