import React, {useEffect, useRef} from 'react';
import Nav from "../../components/Nav/Nav";
import {LocomotiveScrollProvider} from "react-locomotive-scroll";
import Logo from "../../svg/Logo";
import EducationItem from "../../components/EducationItem/EducationItem";
import EducationListUl from "../../components/EducationItem/EducationListUl";
import Direction from "../../components/Direction/Direction";
import Contact from "../../components/Contact/Contact";

const EducationSecondPage = () => {
    const containerRef = useRef(null)

    const scrollingEducation = () => {
    }
    useEffect(() => {
        scrollingEducation()
    }, [])
    return (
        <>

            <LocomotiveScrollProvider
                options={
                    {
                        smooth: true,
                        multiplier: 0.5,
                        direction: "vertical",
                        reloadOnContextChange: false,
                        resetNativeScroll: false,
                    }
                }
                watch={
                    []
                }
                containerRef={containerRef}
            >
                <Nav cssClass={"navigation"}/>
                <section className="education-page" ref={containerRef} data-scroll-container>
                    <div className={'education-hero'}>
                        <div className="education-logo">
                            <Logo color={'#ffffff'} cssClass={'education-logo__svg'}/>
                        </div>
                        <div className="educationPage__content">
                            <div className="wrapper">
                                <h3 className="education__content__title">
                                    Educational activities
                                </h3>
                                <p className="education__content__sub-title">
                                    Avigilon - американська дочірня компанія Motorola Solutions.
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="wrapper">
                        <article className="education__container">
                            <h6 className="title title-education__container"><span>Інтегровані комплекси безпеки на платформі Protégé GX – базові та розширені </span><span>рішення</span>
                            </h6>
                            <p className="education__container__description">
                                На об’єктах дещо більших за заміський будинок чи невеликий офіс зазвичай експлуатується до 10 різних електронних систем, що так чи інакше пов’язані з процедурами безпеки та охорони. При цьому ефективність роботи операторів зворотно пропорційна кількості таких систем, кожна з яких генерує велику кількість інформації без її зв’язку із суміжними комплексами. Побудова інтегрованих рішень – єдино можливий шлях оптимізації потоків інформації та підвищення ефективності роботи операторів та особового складу на середніх та масштабних інсталяціях.
                            </p>
                        </article>
                        <ul className="education-program">
                            <EducationItem title={"Цільова аудиторія"}
                                           description={'проектанти та архітектори систем безпеки, presale-інженери, менеджери проектів, спеціалісти впровадження та експлуатації'}
                            />
                            <EducationItem title={"Тривалість курсу"}
                                           description={'20 академічних годин в залежності від підготовки слухачів. Курс потребує попереднього навчання за програмою «Функціональні можливості сучасних корпоративних СКУД на базі платформ Protégé GX/WX» '}
                            />
                            <EducationItem title={"Вартість"}
                                           description={'Безкоштовно для партнерів та клієнтів ТОВ «Фортісек»'}
                            />

                            <li className="education-program__list">
                                <div className="education-program__item">
                                    <p className="education-program__item__title education-program__item__title--program">01.
                                        ВСТУпна частина</p>
                                    <ul className={'education-program__list--small'}>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">Поняття інтегрованого комплексу безпеки, відмінності від програмних рішень на базі VMS/PSIM</p>
                                        </li>
                                        <li className="education__list-small">
                                            <p className="education_list-small__text">o	Методи інтеграції: апаратна, протокольна, синхронізація баз даних</p>
                                        </li>
                                        {/*<li className="education__list-small education__list-small--no-decoration">*/}
                                        {/*    <img className="education-program__item__list__img"*/}
                                        {/*         src={'/img/competence_dron.jpg'} alt={''}/>*/}
                                        {/*</li>*/}
                                    </ul>
                                </div>
                            </li>
                            <EducationListUl title={"02.  Проектування та побудова апаратної складової ІКБ"}
                                             itemDesc={['Поняття ієрархічних та пласких архітектур: практика вибору', 'Апаратна інтеграція систем внутрішньої, тривожної, периметрової сигналізації, СКУД та управління будівлею (BMS)', 'Особливості використання RS485/Ethernet в якості комунікаційної інфраструктури платформи ICT Protege', 'Розрахунок кластерів та живлення – важливий етап проектування апаратних компонентів']}
                            />
                            <EducationListUl title={"03. Функціонал та налаштування програмного забезпечення "}
                                             itemDesc={['Програмна архітектура Protégé GX та особливості системи ліцензування', 'Вимоги до серверного забезпечення та робочих станцій операторів', 'Налаштування базових елементів системи, практика створення індивідуальних інтерфейсів','Можливості звітності та управління разовими користувачами']}
                            />
                            <EducationListUl title={"04. Інтеграція сторонніх підсистем та застосунків в ІКБ"}
                                             itemDesc={['Поняття подієвого відеоспостереження, можливості інтеграції з VMS', 'Сервіс синхронізації баз даних DataSync –інтеграції з ERP/HR-системами','Інтеграція з домен-контролером Active Directory (оператори та користувачі)', 'Інтеграція з біометричними ідентифікаційними системами','Низькорівнева та протокольна інтеграція з ліфтами: переваги та недоліки підходів','Інтеграція з on-line системами бездротових замків та готельними системами – світова практика'
                                             ,'Високорівнева інтеграція з периметровими системами (дискретні та розподілені сенсорні комплекси)','Підключення сторонніх підсистем за допомогою стандартних промислових протоколів (ModBus, BACNet і т.д.)', 'Інтеграції з банківськими системами безпеки (АТМ) та електронними сейфами зберігання ключів','oнтерфейс оператора ІКБ як SIP-термінал: інтеграція з інтеркомами','Основи роботи з WEB SOAP SDK'
                                             ]}
                            />
                        </ul>
                    </div>
                    <div className="other-ways-education">
                        <h6 className="title title--other-ways-education">інші напрямки навчання</h6>
                        <div className="other-ways-education--container">
                            <Direction/>
                        </div>
                    </div>
                    <Contact cssClass={'other-ways-education--contact'}/>
                </section>
            </LocomotiveScrollProvider>
        </>
    );
};

export default EducationSecondPage;