import React from 'react';
import './competence.scss'
import UlList from "../../components/UlList/UlList";
import CompetencePop from "../../components/CompetencePop/CompetencePop";

const Competence = (props) => {

    return (
        <section className="competence scroll" data-scroll-section id={'competence'} data-scroll>
            <div className="competence-wrapper" data-scrll-target={'#competence'}>
                <div className="competence__content">
                    <div className="competence__content__header">
                        <h5 className="title">компетенції</h5>
                        <p className="competence_sub-title">"Персонал ТОВ "Фортісек" має практичний досвід та надає кваліфіковані консультації в наступних галузях індустрії безпеки:"</p>
                    </div>
                    <ul className="competence__lists">
                        <UlList text={'01. Інтелектуальні системи відеонагляду'}
                                cssClass={'competence__list'}
                                cssSpan={'competence__list__text__decoration'}
                                cssSvg={'competence__list__svg'}
                                color={'#373737'}
                                id={'showFirst'}
                        />
                        <UlList text={'02.  Системи охорони периметру'}
                                cssClass={'competence__list'}
                                cssSpan={'competence__list__text__decoration'}
                                cssSvg={'competence__list__svg'}
                                color={'#373737'}
                                id={'showSecond'}
                        />
                        <UlList text={'03.  Системи контролю і управління доступом'}
                                cssClass={'competence__list'}
                                cssSpan={'competence__list__text__decoration'}
                                cssSvg={'competence__list__svg'}
                                color={'#373737'}
                                id={'showThird'}
                        />
                        <UlList text={'04.   Системи охоронного патрулювання'}
                                cssClass={'competence__list'}
                                cssSpan={'competence__list__text__decoration'}
                                cssSvg={'competence__list__svg'}
                                color={'#373737'}
                                id={'showFourth'}
                        />
                        <UlList text={'05.  Інтегровані комплекси безпеки'}
                                cssClass={'competence__list'}
                                cssSpan={'competence__list__text__decoration'}
                                cssSvg={'competence__list__svg'}
                                color={'#373737'}
                                id={'showFive'}
                        />
                    </ul>
                </div>
                <div className="competence__img">
                    <img src="./img/night_ceety.jpg" alt=""/>
                </div>
            </div>
            <CompetencePop
                title={'Інтелектуальні системи відеонагляду'}
                img={'./img/contact.jpg'}
                id={'showFirst'}
                actions={{
                    ulShow: () => (
                        <ul className="competence-ul">
                            <li className={'competence-item'}><p className={'competence-item__text'}>IP-системи
                                відеонагляду високої чіткості загально призначення</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>IP-системи
                                відеонагляду надвисокої чіткості (8-61Мп)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Тепловізійні
                                системи відеонагляду</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Засоби мобільного
                                відеонагляду</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Спеціальні рішення:
                                вибухобезпечні та антивандальні</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Аналітичні
                                алгоритми охоронного призначення</p></li>
                        </ul>
                    )
                }}/>
            <CompetencePop
                title={'Системи охорони периметру'}
                img={'./img/competence_pc.jpg'}
                id={'showSecond'}
                actions={{
                    ulShow: () => (
                        <ul className="competence-ul">
                            <li className={'competence-item'}><p className={'competence-item__text'}>Мікрохвильові та
                                доплерівські засоби детекції</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Інфрачервоні
                                активні засоби детекції (включаючи комбіновані рішення</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Акселерометричні
                                засоби детекції</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Мікрофонні засоби
                                детекції</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Лазерні оптичні
                                засоби детекції</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Оптоволоконні
                                засоби детекції (інтерферометричні, рефлектометричні)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Комунікаційні
                                IP-екосистеми</p></li>
                        </ul>
                    )
                }}/>
            <CompetencePop
                title={'Системи контролю і управління доступом'}
                img={'./img/competence_dron.jpg'}
                id={'showThird'}
                actions={{
                    ulShow: () => (
                        <ul className="competence-ul">
                            <li className={'competence-item'}><p className={'competence-item__text'}>Системи
                                атрибутивної ідентифікації (125kHz/13,56MHz/900MHz/2,4GHz)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Системи
                                біометричної ідентифікації (дактилоскопія, стереометрія обличчя)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Мобільні платформи
                                ідентифікації (NFC/BLE)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Апаратна
                                інфраструктура СКУД середньої та корпоративної місткості</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Інтерком-системи та
                                оповіщення (SIP)</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Елементи
                                автоматизації вхідних груп (турнікети та шлюзи)</p></li>
                        </ul>
                    )
                }}/>
            <CompetencePop
                title={'Системи охоронного патрулювання'}
                img={'./img/competence_third.jpg'}
                id={'showFourth'}
                actions={{
                    ulShow: () => (
                        <ul className="competence-ul">
                            <li className={'competence-item'}><p className={'competence-item__text'}>Традиційні
                                автономні системи охоронного патрулювання</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Системи
                                патрулювання з моніторингом в режимі реального часу</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Віртуальні системи
                                патрулювання і обліку робочого часу </p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Локальні та хмарні
                                програмні платформи</p></li>
                        </ul>
                    )
                }}/>
            <CompetencePop
                title={'Інтегровані комплекси безпеки'}
                img={'./img/competence_fourth.jpg'}
                id={'showFive'}
                actions={{
                    ulShow: () => (
                        <ul className="competence-ul">
                            <li className={'competence-item'}><p className={'competence-item__text'}>ІКБ високої
                                місткості зі штатними апаратними та програмними інтеграціями</p></li>
                            <li className={'competence-item'}><p className={'competence-item__text'}>Індивідуальні
                                інтеграції по спеціальному замовленню</p></li>
                        </ul>
                    )
                }}/>
        </section>
    );
};

export default Competence;