import React, {useEffect} from 'react';
import Logo from "../../svg/Logo";
import './hero.scss'
import HeroSlider from "../../components/HeroSlider/HeroSlider";
import {useLocomotiveScroll} from "react-locomotive-scroll";


const Hero = () => {
    const {scroll} = useLocomotiveScroll()
    // const sls = new ScrollListScene(wrapperNode);
    let updateScroll = false;
    if (scroll) {
        scroll.update()
        scroll.on("call", (t, x, e) => {
            const nav = document.querySelector('nav')
            if (t === 'hero') {
                if (x === "enter") {
                    if (nav.classList.contains('navigation-black')) {
                        nav.classList.remove('navigation-black')
                    }
                }
                if (x==='exit'){
                    nav.classList.add('navigation-black')
                }
            } else {

            }
        })
    }

    return (
        <>
            <section className="hero" data-scroll data-scroll-repeat data-scroll-section
                     data-scroll-call='hero'>
                <div className="hero_container" data-scroll-target={'#hero'}>
                    <div className="logo--white">
                        <Logo color="white" cssClass={'hero-logo'}/>
                    </div>
                    <div className="hero_container__content">
                        <article className="main-title-container">
                            <h1 className="main-title"><span className="main-title__decoration">FORTISEC</span>Security
                                STUDIO</h1>
                            <p className="main-title__sub-title">Fortisec надають кваліфіковану підтримку партнерів і
                                кінцевих споживачів <span className="main-title__sub-title__decoration">на стадіях передпроектних дослідженнь</span>
                            </p>
                        </article>
                        <div className="hero_container__news">
                            <HeroSlider/>
                        </div>
                    </div>
                </div>
            </section>

        </>
    );
};

export default Hero;