import React from 'react';
import './news.scss'
import Arrow from "../../svg/arrow/Arrow";
import NewsSlider from "../../components/NewsSlider/NewsSlider";
import {Link} from "react-router-dom";

const News = () => {
    return (
        <section className="news" data-scroll-section >
            <h5 className="title category-title--news"><span className="category-title--news__decoration">новини</span></h5>
            <div className="wrapper">
            <div className="new__slide-container">
                <div className="swiper-container--news ">
                    <NewsSlider/>
                </div>
            </div>
            <Link to="/" className="category-link"><span className="category-link__text">ПОСЛУГИ</span> <span
                className="list-items__href__decoration"><Arrow color ="#373737" cssClass ={"category-link__svg"}/> </span></Link>
            </div>
        </section>
    );
};

export default News;