import React, {useEffect} from 'react';
import Arrow from "../../svg/arrow/Arrow";
import './about.scss'


const About = () => {
    return (
        <section className="standard-description about" data-scroll-section id={'about'} >
            <div className="wrapper" >
                <h5 className="title">про нас</h5>
                <p className="category-sub-title"><span className="category-sub-title-desctop">Fortisec – це команда однодумців та професіоналів ринку охоронних
                    систем з індивідуальною практикою не менше 20 років.</span>
                <span className="category-sub-title--mobile">Fortisec – це компанія-консультант та проектний дистриб’ютор</span>
                </p>
                <article className="standard-description__container">
                    <p className="standard-description__container__text">Fortisec – це команда однодумців та
                        професіоналів ринку охоронних систем з індивідуальною практикою не менше 20 років. Перш за
                        все,
                        це люди, що успішно створюють та впроваджують одні з наймасштабніших та найбільш складних
                        інтегрованих комплексів безпеки в Україні. Це ідеї, знання та експертиза, що дозволяють
                        убезпечувати дійсно складні, критично важливі для економіки країни об’єкти та застосовувати
                        цей
                        досвід у Замовників меншого масштабу. </p>
                    <p  className="standard-description__container__text standard-description__container__text--last">Fortisec
                        – це компанія-консультант та проектний дистриб’ютор, що надає кваліфіковані послуги з
                        попереднього вивчення об’єктів, моделювання загроз, створення концепцій охорони за допомогою
                        найсучасніших апаратно-програмних засобів, постачання відповідного обладнання, повного
                        спектру
                        комерційної та технічної підтримки партнерів на всіх етапах життєвого циклу проектів. Це
                        інноваційні і технологічні рішення, що в повній мірі доступні вітчизняним системним
                        інтеграторам. </p>
                </article>
                <p  data-scroll-repeat data-scroll  className="category-link"><span className="category-link__text">БРЕНДИ</span> <span
                    className="list-items__href__decoration"><Arrow color="#373737" cssClass={"category-link__svg"}/>  </span></p>
            </div>
        </section>
    )
}
export default About;